<template>
  <div class="cars_brand">
    <div class="cars_brand_left">
      <dl>
        <p class="all_band_text">全部品牌</p>
        <dt class="hot_line">热门品牌</dt>
        <template v-for="(item,index) in pTocBrand.hotBrandGroupList">
          <dd @click="selsctCars(item)" :class="{'checkActive':item.brandGroupName == isList}">
            <img :src="item.logo" alt="item.brandGroupName">
            <p>{{item.brandGroupName}}</p>
          </dd>
        </template>
      </dl>
      <dl v-for="(item,index) in pTocBrand.brandGroupList">
        <dt ref="dts">{{item.firstLetter}}</dt>
        <dd @click="selsctCars(classList)" v-for="classList in item.data" :class="{'checkActive':classList.brandGroupName == isList}">
          <img :src="classList.logo" alt="">
          <p :id="classList.brandGroupId">{{classList.brandGroupName}}</p>
        </dd>
      </dl>
    </div>

    <div class="cars_brand_content">
      <div class="selectCars">
        <p class="selectTitle">选择车系</p>
        <div class="selectAll_border">
          <!-- <strong class="strong"> </strong> -->
          <span class="selectAll" @click="clearBrand">不限车系</span>
        </div>
        <ul class="selectClass">
          <template v-for="items in hotBrandGroupList">
            <li v-for="(item,index) in items.types" @click="brandClickEvent(index,item.typeFullName,item.typeId)"
                :data-id="item.typeId">
              {{item.typeFullName}}
            </li>
          </template>
        </ul>
      </div>
    </div>

    <ul class="letters">
      <li v-for="(letter,index) in letters" @click="match(letter,index)">{{letter}}</li>
    </ul>
  </div>
</template>

<style lang="scss">
  .cars_brand {
    background: #ffffff;
    width: 100%;
    overflow: hidden;
    min-height: 6.5rem;
    position: absolute;
    top:.7rem;
    z-index: 2;
    .cars_brand_left {
      //width: 2.8rem;
      //float: left;
      width: 100%;
      background: #fff;
      height: 2.9rem;
      overflow-y: auto;
      position: absolute;
      z-index: 2;
      -webkit-overflow-scrolling: touch;
      dl {
        background: #fff;
        dt {
          display: block;
          height: .5rem;
          line-height: .5rem;
          background: #eeeeee;
          padding-left: .24rem;
          font-size: .22rem;
          width: 89%;
        }
        .all_band_text{
            padding-left: .24rem;
            font-size: .26rem;
            line-height: .66rem;
            color: #666;
        }
        .hot_line{
            width: 89%;
        }
        dd {
          height: .8rem;
          line-height: .8rem;
          border-bottom: 1px solid #ececec;
          background: #fff;
          display: flex;
          width: 92%;
          img {
            width: .46rem;
            height: .46rem;
            margin: .16rem .12rem 0 .24rem;
          }
          p {
            font-size: .22rem;
          }
        }
        dd.checkActive {
          background: #eee;
        }
      }
    }
    .cars_brand_content {
    //   float: left;
      padding-bottom: 999px;
      margin-bottom: -999px;
      background: #ffffff;
      width: 4.7rem;
      position: absolute;
      right: 0;
      top:0;
      z-index: 0;
      border-left: 1px solid #ececec;
      .selectCars {
        background: #ffffff;
        .selectTitle {
          background: #eeeeee;
          font-size: .22rem;
          height: .5rem;
          line-height: .5rem;
          padding-left: .2rem;
          color: #676767;
        }
        .selectAll_border{
          border-bottom: 1px solid #eee;
          display: flex;
          width: 85%;
          text-indent: 1.8em;
        //   .strong{
        //      width: .3rem;
        //      height: .5rem;
        //      background: url('noCar.png') no-repeat;
        //      background-size: .24rem;
        //      margin:.26rem 0 0 .46rem
        //   }
        }
        .selectAll {
          display: block;
          height: .6rem;
          width: 1.8rem;
          border-radius: .2rem;
          text-align: center;
          line-height: .6rem;
          font-size: .24rem;
          margin: .1rem 0 0 0;
        }
      }
      .selectClass {
        display: flex;
        flex-direction: column;
        padding-bottom: .5rem;
        li {
          display: flex;
          justify-content: flex-start;
          height: .6rem;
          border-bottom: 1px solid #ececec;
          text-align: center;
          line-height: .6rem;
          font-size: .22rem;
          text-indent: 2em;
          width: 85%;
        }
      }
    }
    .letters {
      position: absolute;
      right: 0.24rem;
      top: 1.1rem;
      z-index: 2;
      li{
        font-size: .2rem;
        margin-bottom:.1rem;
      }
    }
  }
</style>

<script>
  export default{
    data () {
      return {
        pTocBrand:{},
        brandList: [],
        hotBrandGroupList: [],
        brandGroupList: [],
        isList: [],
        brandGroupId: '',
        letters: [],
        showbrandtypes:[]
      }
    },
    mounted(){
        var _this = this;
        var city = window.localStorage.getItem("city"),reg = new RegExp('"',"g"),
            token = '',
            storage = window.localStorage;
        if(city != null){
          city = city.replace(reg, "");
          city = Number(city);
        }else{
          window.localStorage.setItem("city", "370100");
        }
        if(storage.getItem("user")){
            token = JSON.parse(storage.getItem("user")).token;
        }
        var _city = {city:city,token:token,currentPage:this.currentPage,pagesize:10};
        var heights = $(window).height();

        $('.cars_brand .cars_brand_left').css("height",heights)
        $('.cars_brand').css("min-height",heights);

        $.ajax({
            type: "POST",url: _this.global.host + "/model/brandGroupListByCity",
            data: _city,success: function (response) {
              if(response.resultCode == '0000'){
                _this.pTocBrand = response.result;
                var jsonList = _this.pTocBrand.brandGroupList,
                    map = {},
                    listA = [],
                    dest = [];
                if (jsonList) {
                  for (var i = 0; i < jsonList.length; i++) {
                    var ai = jsonList[i];
                    if (!map[ai.firstLetter]) {
                      dest.push({
                        firstLetter: ai.firstLetter,
                        data: [ai]
                      });
                      map[ai.firstLetter] = ai;
                    } else {
                      for (var j = 0; j < dest.length; j++) {
                        var dj = dest[j];
                        if (dj.data[0].firstLetter == ai.firstLetter) {
                          dj.data.push(ai);
                          break;
                        }
                      }
                    }
                  }
                }
                dest.sort(getSortFun('asc', 'firstLetter'));
                function getSortFun(order, sortBy) {
                  var ordAlpah = (order == 'asc') ? '>' : '<';
                  var sortFun = new Function('a', 'b', 'return (a.' + sortBy + ').charCodeAt(0)' + ordAlpah + '(b.' + sortBy + ').charCodeAt(0)' + '?1:-1');
                  return sortFun;
                }
                _this.pTocBrand.brandGroupList = dest;
                for (var i = 0; i < _this.pTocBrand.brandGroupList.length; i++) {
                  _this.letters.push(_this.pTocBrand.brandGroupList[i].firstLetter);
                  console.log(_this.letters);
                }
              }else if(response.resultCode == '1111'){}
            }
        });
    },
    methods: {
      match(letter, index){
        for (var i = 0; i < this.pTocBrand.brandGroupList.length; i++) {
          if (this.pTocBrand.brandGroupList[i].firstLetter == letter) {
            this.selsctCars(this.pTocBrand.brandGroupList[i].data[0]);
            $('.cars_brand_left').animate({scrollTop: this.$refs.dts[i].offsetTop}, 400);
          }
        }
      },
      brandClickEvent(index, typeFullName, typeId){
        var motorcycleType = typeFullName;
        var brandname = this.showbrandtypes.brandname;
        var brandid = this.showbrandtypes.brandid;
        var False = false;
        this.$emit('brandClickEvent', motorcycleType, typeId, brandname, brandid, False);
      },
      clearBrand(){
        var False = false;
        var motorcycleType = this.isList;
        var brandGroupId = this.brandGroupId;
        this.$emit('clearBrand', motorcycleType, brandGroupId, False);
      },
      hideBrand(){
        var False = false;
        this.$emit('hideBrand', False);
      },
      selsctCars(classList){
        var _this = this;
        this.isList = classList.brandGroupName;
        this.showbrandtypes.brandname = classList.brandGroupName;
        this.showbrandtypes.brandid = classList.brandGroupId;
        var brandGroupId = classList.brandGroupId;
        this.brandGroupId = classList.brandGroupId;
        var citys = window.localStorage.getItem("city");
        var reg = new RegExp('"', "g");
        citys = citys.replace(reg, "");
        //获取车系
        $.ajax({
          type: "POST", url: this.global.host + "/model/brandAndTypesByBrandGroupIdAndCity",
          data: {city: citys, brandGroupId: brandGroupId},
          success: function (response) {
            if (response.resultCode == '0000') {
              _this.hotBrandGroupList = response.result;
              $('.cars_brand_content').css('z-index',2);
            } else if (response.resultCode == '1111') {
            }
          }
        });
      }
    }
  }
</script>



